import React, { Component, Suspense } from 'react';
const EmbedCode = React.lazy(() => import('../../../PublishCommonComponents/EmbedCode'));
class HtmlEmbed extends Component {

    render() {
        const { data } = this.props
        return <div className="card text-decoration-none">
            <div className="card-body p-lg-7">
                <div className="m-auto">
                    <div className="embed_code">
                        <h5 className="mb-5 fw-bolder">Embed Widget On HTML Website</h5>
                        <h6>1. Adjust the width and height of the widget as per your requirement</h6>
                        <Suspense fallback={''}>
                            <EmbedCode copyText={'2. Copy the generated embed code for your HTML Website'} data={data} />
                        </Suspense>
                        <h6 className="mb-5">3. Paste the publish code into the backend of the page</h6>
                        <ul className="list-unstyled list-points fs-7">
                            <li className="mb-3">Simply log in to your HTML website and select the landing page where you wish to embed your widget</li>
                            <li className="mb-3">Paste the embed code into the backend of the page</li>
                            <li className="mb-3">Apply the required changes and Voila! You will have successfully published the widget on your website</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    }
}

export default HtmlEmbed;